.userinput{
  width: 80%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 0.3rem;
  border-radius: 5px;
  font-family: 'Nunito', sans-serif;
}

.btn {
  font-family: 'Nunito', sans-serif;
  font-size: 0.8rem;
  border-radius: 3px;
  border: 1px solid #E85D75;
  box-shadow: 2px 2px lightgrey;
  cursor: pointer; 
  margin-top: 1vh;
  
}

.btn:hover {
  background-color: #E85D75;
  color: white;
}

form{
  background-color: transparent;
  padding-top:6vh;
  padding-bottom:6vh;
  width: 40%;
  display: block;
  margin: auto;
  border: 2px solid #E85D75;
  border-radius: 30px;

}