.App {
  text-align: justify;
  margin: auto;
}

.App-link {
  color: #61dafb;
}

* {
  box-sizing: border-box;
}

body {
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  font-family:  helvetica, arial, sans-serif;
  margin: 2em;
  background-color: transparent;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  color: #282c34;
 
}

h1 {
 font-family: helvetica, arial, sans-serif;
 color: #ff5050;
 text-align: center;
}


p {
  font-family:  helvetica, arial, sans-serif;
  font-size: 22px;
  color: #000000;
  text-align: center;  
}

.bold {
  font-weight: bold;
}


button:hover {
  background-color: lightgrey;
}

button:active {
  box-shadow: none;
}

li {
  margin-bottom: 5px;
}

footer {
  margin-top: 50px;
  padding-top: 25px;
  border-top: 1px solid lightgrey;
}


